export const LOGIN = "LOGIN";
export const LOGOFF = "LOGOFF";

export const login = (userData: object, auth_token: String) => {
  return {
    type: LOGIN,
    userData,
    authToken: auth_token,
  };
};

export const logoff = () => {
  return {
    type: LOGOFF,
  };
};

